	module.exports = {"data":{"challengeNode":{"title":"Specify How Fonts Should Degrade","description":["所有浏览器都有几种默认字体。这些通用字体包括<code>monospace</code>，<code>serif</code>和<code>sans-serif</code>。","当字体不可用，你可以告诉浏览器通过 “降级” 去使用其他字体。","例如，如果你想将一个元素的字体设置成<code>Helvetica</code>，当<code>Helvetica</code>不可用时，降级使用<code>sans-serif</code>字体，那么可以这样写：","<blockquote>p {<br>&nbsp;&nbsp;font-family: Helvetica, sans-serif;<br>}</blockquote>","通用字体名字不区分大小写。同时，也不需要使用引号，因为它们是 CSS 关键字。","<hr>","首先，添加<code>monospace</code>字体到<code>h2</code>元素里，它现在拥有着<code>Lobster</code>和<code>monospace</code>两种字体。","在上一个挑战里，你已经通过<code>link</code>标签引入谷歌<code>Lobster</code>字体。现在让我们注释掉谷歌<code>Lobster</code>字体的引入（使用我们之前学过的<code>HTML</code>注释），使字体失效。你会发现你的<code>h2</code>元素降级到了<code>monospace</code>字体。","<strong>注意：</strong><br>如果你的电脑已经安装了<code>Lobster</code>字体，你将看不到这个降级过程，因为你的浏览器会找到该字体。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/basic-css/specify-how-fonts-should-degrade","blockName":"Basic CSS","tests":[{"text":"<code>h2</code>元素应该含有<code>Lobster</code>字体。","testString":"assert($(\"h2\").css(\"font-family\").match(/^\"?lobster/i), '<code>h2</code>元素应该含有<code>Lobster</code>字体。');"},{"text":"当<code>Lobster</code>字体失效时，<code>h2</code>元素应该降级使用<code>monospace</code>字体。","testString":"assert(/\\s*h2\\s*\\{\\s*font-family\\:\\s*(\\'|\")?Lobster(\\'|\")?,\\s*monospace\\s*;\\s*\\}/gi.test(code), '当<code>Lobster</code>字体失效时，<code>h2</code>元素应该降级使用<code>monospace</code>字体。');"},{"text":"通过添加<code>&#60!--</code>，注释掉谷歌<code>Lobster</code>字体的引入。","testString":"assert(new RegExp(\"<!--[^fc]\", \"gi\").test(code), '通过添加<code>&#60!--</code>，注释掉谷歌<code>Lobster</code>字体的引入。');"},{"text":"确保注释要以<code>--&#62;</code>结束。","testString":"assert(new RegExp(\"[^fc]-->\", \"gi\").test(code), '确保注释要以<code>--&#62;</code>结束。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<link href=\"https://fonts.googleapis.com/css?family=Lobster\" rel=\"stylesheet\" type=\"text/css\">\n<style>\n  .red-text {\n    color: red;\n  }\n\n  h2 {\n    font-family: Lobster;\n  }\n\n  p {\n    font-size: 16px;\n    font-family: monospace;\n  }\n</style>\n\n<h2 class=\"red-text\">CatPhotoApp</h2>\n<main>\n  \n  <a href=\"#\"><img src=\"http://cdn.freecodecamp.cn/relaxing-cat.jpg\" alt=\"一只仰卧着的萌猫\"></a>\n  \n  <p>猫咪最喜欢的三件东西：</p>\n  <ul>\n    <li>猫薄荷</li>\n    <li>激光笔</li>\n    <li>千层饼</li>\n  </ul>\n  <p>猫咪最讨厌的三件东西：</p>\n  <ol>\n    <li>跳蚤</li>\n    <li>打雷</li>\n    <li>同类</li>\n  </ol>\n  \n  <form action=\"/submit-cat-photo\">\n    <label><input type=\"radio\" name=\"indoor-outdoor\">室内</label>\n    <label><input type=\"radio\" name=\"indoor-outdoor\">室外</label><br>\n    <label><input type=\"checkbox\" name=\"personality\">忠诚</label>\n    <label><input type=\"checkbox\" name=\"personality\">懒惰</label>\n    <label><input type=\"checkbox\" name=\"personality\">积极</label><br>\n    <input type=\"text\" placeholder=\"猫咪图片地址\" required>\n    <button type=\"submit\">提交</button>\n  </form>\n</main>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/basic-css/size-your-images","id":"bad87fee1348bd9aedf08808"},"slug":"/responsive-web-design/basic-css/specify-how-fonts-should-degrade"}}

